<template>
  <div class="project-container">
    <h1 class="fuente-yo">{{ projectName }}</h1>
    
    <div class="project-details">
      <div class="project-section">
        <h3 class="fuente-uno">Description:</h3>
        <p class="fuente-uno" style="text-align: justify;">{{ projectDescription }}</p>
      </div>

      <div class="project-section">
        <h3 class="fuente-uno" style="margin-top: 20px">Frameworks/Tools:</h3>
        <span class="fuente-uno">{{ projectTools }}</span>
      </div>

      <div class="subcollections fuente-uno">
        <h3 class="fuente-uno">Features:</h3>
        <ul>
          <li style="text-align: justify;" v-for="(feature, index) in projectFeatures" :key="index">
            {{ feature }}
          </li>
        </ul>
      </div>

      <img :src="project.subcollections.images.img00" class="d-block w-100" :alt="project.subcollections.features.feature00">

    </div>
  </div>
</template>

<script>
export default {
  props: {
    project: Object,
    main: Number
  },
  computed: {
    projectName() {
      return this.project?.project?.name || 'Nombre no disponible';
    },
    projectDescription() {
      return this.project?.project?.overview || 'Descripción no disponible';
    },
    projectTools() {
      return this.project?.project?.tools || 'Herramientas no disponibles';
    },
    projectFeatures() {
      return this.project?.subcollections?.features || [];
    }
  }
};
</script>

<style scoped>
.project-container {
  display: flex;
  align-items: center;
  flex-direction: column;
  max-width: 800px;
  margin: 0 auto;
}

.project-details {
  width: 100%;
}

.project-section {
  margin-bottom: 20px;
}

.subcollections {
  margin-top: 20px;
}

.type-projects {
  display: flex;
  justify-content: center;
}

.navbar-menu {
  background: #233043;
}
</style>
