<template>
    <div>
      <h2 class="type-projects fuente-yo">PROJECT TYPES</h2>
      <nav class="navbar navbar-expand-lg navbar-dark navbar-menu">
        <div class="container-fluid d-flex justify-content-center">
          <ul class="navbar-nav">
            <li class="nav-item dropdown">
              <a class="nav-link dropdown-toggle d-flex justify-content-center align-items-center fuente-uno" href="#" id="navbarDropdown1" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                JOBS
              </a>
              <ul class="dropdown-menu dropdown-menu-dark" aria-labelledby="navbarDropdown1">
                <li><a class="dropdown-item fuente-uno" href="#">Action 1</a></li>
                <li><a class="dropdown-item fuente-uno" href="#">Another action 1</a></li>
                <li><a class="dropdown-item fuente-uno" href="#">Something else here 1</a></li>
              </ul>
            </li>
            <li class="nav-item dropdown">
              <a class="nav-link dropdown-toggle d-flex justify-content-center align-items-center fuente-uno" href="#" id="navbarDropdown2" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                FREELANCE
              </a>
              <ul class="dropdown-menu dropdown-menu-dark" aria-labelledby="navbarDropdown2">
                <li><a class="dropdown-item fuente-uno" href="#">Action 2</a></li>
                <li><a class="dropdown-item fuente-uno" href="#">Another action 2</a></li>
                <li><a class="dropdown-item fuente-uno" href="#">Something else here 2</a></li>
              </ul>
            </li>
            <li class="nav-item dropdown">
              <a class="nav-link dropdown-toggle d-flex justify-content-center align-items-center fuente-uno" href="#" id="navbarDropdown3" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                HOBBY
              </a>
              <ul class="dropdown-menu dropdown-menu-dark" aria-labelledby="navbarDropdown3">
                <li><a class="dropdown-item fuente-uno" href="#">Action 3</a></li>
                <li><a class="dropdown-item fuente-uno" href="#">Another action 3</a></li>
                <li><a class="dropdown-item fuente-uno" href="#">Something else here 3</a></li>
              </ul>
            </li>
          </ul>
        </div>
      </nav>
    </div>
    <div style="display: flex; flex-direction: column; align-items: center;">
      <ProjectComp class="project" v-for="(project, index) in projectsWithSubcollections" :key="index" :project="project" :main="index"/>        
    </div>
  </template>

<script>
  import db from '../../../utils/firebase'
  import { collection, getDocs, query, where, onSnapshot } from "firebase/firestore"; // Importa onSnapshot para escuchar cambios en Firestore
  import { onMounted, reactive, ref } from 'vue';
  import ProjectComp from "./ProjectComp.vue"

  export default {
      components: {
        ProjectComp
      },
      setup(){
        const projectsWithSubcollections = ref([]);

        // Utiliza onMounted para cargar los datos iniciales de la colección
        onMounted(async () => {
          try {
          await fetchProjectsData();
          } catch (error) {
            console.error('Error fetching initial projects data:', error);
          }
          });

        // Utiliza onMounted para suscribirse a cambios en la colección
        onMounted(() => {
          const unsubscribe = onSnapshot(collection(db, 'projects'), async () => {
          try {
              await fetchProjectsData();
          } catch (error) {
              console.error('Error fetching projects data:', error);
          }
          });

          // Retorna una función de limpieza para detener la escucha de cambios cuando sea necesario
          return unsubscribe;
        });

        // Función para cargar los datos de los proyectos y sus subcolecciones
        async function fetchProjectsData() {
          const projectsArray = [];
          const projectsQuery = query(collection(db, 'projects'), where("main", "==", true));
          //const projectsQuerySnapshot = await getDocs(collection(db, 'projects'));
          const projectsQuerySnapshot = await getDocs(projectsQuery);
          
          for (const projectDoc of projectsQuerySnapshot.docs) {
          const projectWithData = {
              project: projectDoc.data(),
              subcollections: {}
          };
          
          const subcollectionsQuerySnapshot = await getDocs(collection(projectDoc.ref, 'projectData'));

          subcollectionsQuerySnapshot.forEach(subcollectionDoc => {
              projectWithData.subcollections[subcollectionDoc.id] = subcollectionDoc.data();
          });

          projectsArray.push(projectWithData);
          }

          // Ordena los proyectos poniendo los que tienen type == "Job" al principio, seguido de "Freelance" y luego "Hobby"
          projectsArray.sort((a, b) => {
            const typeOrder = ["Job", "Freelance", "Hobby"];
            return typeOrder.indexOf(a.project.type) - typeOrder.indexOf(b.project.type);
          });
          projectsWithSubcollections.value = projectsArray;
        }

        return {
            projectsWithSubcollections
        };
      }
  }
</script>

<style scoped>
.type-projects{
    display: flex;
    justify-content: center;
}
.navbar-menu{
    background: #233043;
    padding-bottom: 20px;
}

.project{
  margin: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
}
</style>